export default ({count, title}) => (
    <div className="flex flex-col justify-center items-center p-4 border-r border-l border-gray-200 bg-primary">
        <div className="text-3xl text-white">{count}</div>
        <div className="w-6 h-2 bg-secondary rounded-lg font-semibold my-4"></div>
        <div className="text-gray-400">{title}</div>
    </div>
)